.counter {
    height: 100vh;
    background-color: #2a2c36;
    display: flex;
    justify-content: center;
    align-items: center;
}

.inner {
    width: 500px;
    height: 400px;
    border: 3px solid #6ee4ff;
    border-radius: 10px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.control {
    width: 460px;
    height: 135px;
    padding: 20px;
    border: 3px solid #6ee4ff;
    border-radius: 10px;
    background-color: #2a2c36;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
